import { Form } from './common/Form';
export class Order extends Form {
    constructor(blockName, container, events) {
        super(container, events);
        this.blockName = blockName;
        this.events = events;
        this._card = container.elements.namedItem('card');
        this._cash = container.elements.namedItem('cash');
        if (this._cash) {
            this._cash.addEventListener('click', () => {
                this._cash.classList.add('button_alt-active');
                this._card.classList.remove('button_alt-active');
                this.onInputChange('payment', 'cash');
            });
        }
        if (this._card) {
            this._card.addEventListener('click', () => {
                this._card.classList.add('button_alt-active');
                this._cash.classList.remove('button_alt-active');
                this.onInputChange('payment', 'card');
            });
        }
    }
    disablingButtonHighlighting() {
        this._cash.classList.remove('button_alt-active');
        this._card.classList.remove('button_alt-active');
    }
}
/*
 * Класс, описывающий контакты
 * */
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
}
