import { Model } from './base/Model';
export class Product extends Model {
}
/*
 * описывает состояние прилодения
 * */
export class AppData extends Model {
    constructor() {
        super(...arguments);
        // массив выбранных продуктов
        this.basket = [];
        this.order = {
            payment: '',
            address: '',
            phone: '',
            email: '',
            total: null,
            items: [],
        };
        this.formErrors = {};
    }
    addItemToOrder(item) {
        this.basket.push(item);
        this.setItemsOrders();
    }
    removeFromOrder(id) {
        this.basket = this.basket.filter((i) => i.id !== id);
    }
    clearBasket() {
        this.basket.length = 0;
    }
    getTotalBasket() {
        return this.basket.length;
    }
    setItemsOrders() {
        this.order.items = this.basket.map((item) => item.id);
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('orderFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        this.formErrors = errors;
        this.events.emit('contactsFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    updatePayment() {
        this.order = {
            items: [],
            total: null,
            address: '',
            email: '',
            phone: '',
            payment: '',
        };
    }
    getBasketPrice() {
        return this.basket.reduce((total, item) => total + (item.price || 0), 0);
    }
    setBasketStore(items) {
        this.catalog = items.map((item) => new Product(Object.assign(Object.assign({}, item), { selected: false }), this.events));
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    selectToOrder() {
        this.catalog.forEach((item) => (item.selected = false));
    }
}
